@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.header-detail{
  padding: 3% 1% 1% 2%;
  background-color: #060b26;
  margin-top: 10px;
}
.main-head {
  margin-top: 120px;
 
}

.page-div{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #F6FCF9;
  padding: 2%;
}
.page-body{
  flex-basis: 60%;

}

.section-gap{
  margin-bottom: 4rem;
}

@media only screen and (min-width: 960px) {
.close-bar {
display: none;  
}
}
@media only screen and (max-width: 760px) {
  .page-body{
    flex-basis: 80%; 
  }
}
.top-navbar {
  background-color: #060b26;
  height: 120px;
  /* display: flex;
  justify-content: start;
  align-items: center; */
  /* overflow: hidden; */
  position: fixed;
  /* top: 0; */
  width: 100%;  
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: #060b26;
  width: 289px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

/* .nav-text a:hover {
  background-color: #1a83ff;
} */

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}

.nav-text >.current-link{
  background-color: #1a83ff;
}
